import React, { useState, useEffect } from 'react';
// import connectPromise from '@vkontakte/vkui-connect-promise';
import connect from '@vkontakte/vkui-connect-promise';
import { View } from '@vkontakte/vkui';

import { ROUTES } from './config';

import Home from './Panels/Home';
import Area from './Panels/Area';
import Task from './Panels/Task';

const location = window.location.hash.substr(1);

const App = () => {
  const [activePanel, setActivePanel] = useState(
    ~ROUTES.indexOf(location) ? location : 'home'
  );
  const [activeArea, setActiveArea] = useState(null);
  const [activeTask, setActiveTask] = useState(null);
  const [popout, setPopout] = useState(null);
  // const [user, setUser] = useState(null);

  const getAuthToken = async () => {
    try {
      const res = await connect.send('VKWebAppGetAuthToken', {
        app_id: 6977723,
        scope: 'status,friends'
      });
      console.log({ res });
    } catch (e) {
      console.error({ e });
    }
  };

  const getUserInfo = async () => {
    try {
      const res = await connect.send('VKWebAppGetUserInfo', {});
      console.log({ res });
    } catch (e) {
      console.error({ e });
    }
  };

  useEffect(() => {
    getAuthToken();
    getUserInfo();
  }, []);

  const setLocation = route => {
    if (route !== 'home') {
      connect
        .send('VKWebAppSetLocation', { location: route })
        .then(e => console.log({ e }));
    } else {
      connect.send('VKWebAppSetLocation', { location: '' });
    }

    setActivePanel(route);
  };

  return (
    <View activePanel={activePanel} popout={popout}>
      <Home id="home" setLocation={setLocation} setActiveArea={setActiveArea} />
      <Area
        id="area"
        activeArea={activeArea}
        setLocation={setLocation}
        setPopout={setPopout}
        setActiveTask={setActiveTask}
      />
      <Task
        id="task"
        activeArea={activeArea}
        activeTask={activeTask}
        setLocation={setLocation}
        setPopout={setPopout}
        setActiveTask={setActiveTask}
      />
    </View>
  );
};

export default App;
